.waytocreate{
    position: relative;
    padding-top: 80px;
    padding-bottom: 100px;
    background: white;
    .section-decor{
        top: 231px;
    }
    &__title{
        max-width: 322px;
        line-height: 113%;
        margin-bottom: 105px;
        padding-left: 52px;
        &:before{
            display: none;
        }
    }
    &__bg{
        width: 100%;
        max-width: 1228px;
        height: 596px;
        position: absolute;
        top: 80px;
        left: 50%;
        transform: translateX(-50%);
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__slider{
        overflow: visible;
    }

}
.waytocreate-content{
    &-wrapper{
        position: relative;
        padding: 48px 52px 0 0;
        &::after{
            position: absolute;
            content: '';
            width: 185px;
            height: 42px;
            background: url(../img/dots.svg);
            background-size: cover;
            background-position: center;
            top: 50px;
            right: -20px;
        }
    }
    &__inner{
        width: 100%;
        max-width: 1124px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        justify-content: space-between;
    }
    &__left{
        padding-top: 113px;
        width: 100%;
        max-width: 365px;
        position: relative;
        &::before{
            position: absolute;
            content: '';
            width: 5px;
            height: 124px;
            background: $c-red;
            left: -52px;
            top: 100px;
        }
        h4{
            font-weight: bold;
            font-size: 38px;
            line-height: 110%;
            color: $text-color;
            margin-bottom: 21px;
        }
        p{
            font-size: 14px;
            line-height: 200%;
        }
    }
    &__right{
        position: relative;
        z-index: 1;
        &::after{
            position: absolute;
            content: '';
            width: 100%;
            max-width: 548px;
            height: 254px;
            bottom: -10px;
            left: 50%;
            transform: translateX(-50%);
            z-index: -1;
            background: #8B8B8B;
            opacity: 0.78;
            filter: blur(160px);
        }
    }
}
.img-way{
    position: relative;
    z-index: 3;
    height: 746px;
    background: transparent;
    .section-decor{
        top: 263px;
    }
    &__img{
        position: absolute;
        top: -604px;
        left: 0;
        width: 100%;
        pointer-events: none;
    }
}
@media screen and (max-width: 1740px){
    .img-way{
        &__img{
            top: -517px;
        }
    }
}
@media screen and (max-width: 1440px){
    .img-way{
        &__img{
            top: -417px;
        }
    }
}

@media screen and (min-width: 1920px){
    .img-way{
        &__img{
            top: -650px;
        }
    }
}