.benefits{
   position: relative;
   &__title{
      padding-left: 52px;
      max-width: 344px;
      line-height: 113%;
      margin-bottom: 57px;
      &::before{
         transform: translate(0%,-272%);
         left: 0;
      }
   }
}
.benefits-list{
   width: 100%;
   display: flex;
   justify-content: space-between;
   flex-wrap: wrap;
   &__item{
      width: 100%;
      max-width: 604px;
      height: 280px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 20px 30px;
      margin-bottom: 20px;
      svg{
         margin-bottom: 15px;
      }
      h4{
         text-align: center;
         width: 100%;
         max-width: 271px;
         font-weight: 800;
         font-size: 28px;
         line-height: 110%;
         margin-bottom: 10px;
      }
      p{
         text-align: center;
         font-size: 14px;
         line-height: 140%;
      }
      &-inner{
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
         width: 100%;
         max-width: 362px;
      }
      &-1{
         background: $c-red;
         height: 480px;
         h4, p{
            color: white;
         }
         p{
            max-width: 319px;
         }
      }
      &-2,&-3{
         background: $c-lblue;
         h4{
            color: $c-red;
         }
         p{
            color: $text-color;
         }
      }
      &-4{
         background: $c-dblue;
         height: 480px;
         margin-top: -200px;
         h4{
            max-width: 340px;
         }
         h4,p{
            color: white;
         }
      }
      &-5{
         background: $c-dblue;
         h4,p{
            color: white;
            span{
               color: $c-red;
            }
         }
         p{
            max-width: 308px;
         }
      }
      &-6{
         background: $c-fiolet;
         h4,p{
            color: white;
         }
         p{
            max-width: 356px;
         }
      }
   }
}

@media screen and (min-width: 1921px){
    .benefits{
       z-index: 4;
       background: white;
    }
}