.header{
  &__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__left{
    a{
      line-height: 98px;
      svg{
        transform: translate(-1px, 5px);
      }
    }
  }
  &__logo{
    &-letter{
      transition: 0.3s;
    }
    &:hover{
      .header__logo-letter{
        fill: $c-red;
      }
    }
  }
  &__right{
    display: flex;
    align-items: center;
    a{
      letter-spacing: -0.2px;
    }
  }
  &__nav{
    display: flex;
    list-style: none;
    border-right: 1px solid rgba(0,0,0, 0.1);
    padding-right: 10px;
    margin-right: 40px;

    &-link{
      font-weight: 800;
      font-size: 14px;
      line-height: 18px;
      margin-right: 31px;
      transition: 0.3s;
      &:hover{
        color: $c-red;
      }
    }

  }
  &__login{
    display: flex;
    font-weight: 800;
    font-size: 14px;
    line-height: 17px;
    a{
      margin-right: 22px;
      transition: 0.3s;
      &:last-child{
        margin-right: 0;
      }
      &:hover{
        color: $c-red;
      }
    }
  }
}
.burger{
  
  width: 25px;
  height: 9px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
  span{
    width: 100%;
    height: 2px;
    background: $text-color;
  }
}
.body-home-page{
  .header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3;
    color: white;
    &__logo{
      &-letter{
        fill: white;
        transition: 0.3s;
      }
      &:hover{
        .header__logo-letter{
          fill: $c-red;
        }
      }
    }
    &__nav{
      border-right: 1px solid white;
    }
  }
}
@media screen and (max-width: $mobile-big){
  .header{
    &__left{
      a{
        line-height: 60px;
        img{
          width: 92px;
          transform: translate(-1px,7px);
        }
      }
    }
    &__right{
      display: none;
    }
  }
  .burger{
    display: flex;
  }
}
@media screen and (max-width: $mobile-big){
  .header{
    &__container{
      padding: 0 20px;
    }

  }
}