.btn {
  position: relative;
  display: inline-block;
  min-width: 240px;
  width: auto;
  height: 64px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  font-family: inherit;
  font-size: 18px;
  line-height: 22px;
  font-weight: 700;
  color: #fff;
  z-index: 1;
  transition: .25s linear;
  transition-property: background-color, border-color;
  &__inner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    border-radius: 40px;
    padding: 0 15px;
    overflow: hidden;
    z-index: 2;
    transition: .25s linear;
    transition-property: color, background-color;
    svg {
      fill: #fff;
      margin-left: 16px;
      transition: .25s linear;
      transition-property: fill;
    }
  }
  &__border {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      //top: 0;
      //bottom: 0;
      top: -1px;
      bottom: -1px;
      background: radial-gradient(94.47% 403.82% at 97.6% 246.88%, #FF8C00 0%, #FF37AD 57.29%, #FFE300 100%);
      opacity: 0;
      z-index: 1;
      border-radius: 40px;
      overflow: hidden;
      @include transition(opacity);
    }
    svg {
      width: 100%;
      height: 100%;
      overflow: visible;
      //width:calc(100% - 2px);
      //height:calc(100% - 2px);
      position: relative;
      rect {
        //width:calc(100% - 2px);
        //height:calc(100% - 2px);
      }
    }
    .tablet,
    .mob {
      display: none;
    }
  }
  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: radial-gradient(94.47% 403.82% at 97.6% 246.88%, #FF8C00 0%, #FF37AD 57.29%, #FFE300 100%);
    filter: blur(24px);
    border-radius: 40px;
    opacity: 0;
    @include transition(opacity);
  }
  &:hover,
  &:active {
    &::before {
      opacity: 1;
    }
    .btn {
      &__border {
        &::after {
          opacity: 1;
        }
      }
    }
  }

  &--sm {
    font-size: 16px;
    line-height: 19px;
    min-width: 208px;
    height: 48px;
    .btn {
      &__inner {
        svg {
          margin-left: 8px;
        }
      }
    }
  }
  &--secondary {
    color: $text-color;
    .btn {
      &__inner {
        svg {
          fill: $text-color;
        }
      }
    }
    &:hover {
      color: #fff;
      .btn {
        &__inner {
          svg {
            fill: #fff;
          }
        }
      }
    }
  }
  &--checkbox {
    font-size: 24px;
    line-height: 29px;
    font-weight: normal;
    color: rgba(255,255,255,.8);
    &::before {
      display: none;
    }
    input {
      display: none;
      &:checked ~ .btn {
        &__border {
          opacity: 1;
        }
        &__inner {
          color: rgba(255,255,255,1);
        }
      }
    }
    .btn {
      &__border {
        opacity: 0;
        @include transition(opacity);
        &::after {
          display: none;
        }
      }
    }
    &:hover {
      color: #fff;
      .btn {
        &__border {
          opacity: 1;
        }
      }
    }
  }
}
button::-moz-focus-inner {
  border: 0;
}

.btn-circle {
  position: relative;
  display: inline-block;
  width: 56px;
  height: 56px;
  cursor: pointer;
  img {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    pointer-events: none;
  }
  svg {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    fill: #fff;
    margin-top: 1px;
  }
}

@media (max-width: 1440px) {
  .btn {
    &--sm {
      font-size: 14px;
      line-height: 17px;
      min-width: 192px;
      height: 40px;
      .btn {
        &__border {
          span {
            display: none;
          }
          span.tablet {
            display: inline;
          }
        }
      }
    }
  }
}
@media (max-width: $desktop) {
  .btn {
    &--checkbox {
      min-width: 144px;
      .btn {
        &__inner {
          padding: 0 20px;
        }
      }
    }
  }
}
@media (max-width: $tablet) {
  .btn {
    &__border {
      span {
        display: none;
      }
      span.tablet {
        display: inline;
      }
    }
    &--sm {
      font-size: 14px;
      line-height: 17px;
      min-width: 192px;
      height: 40px;
    }
    &--checkbox {
      font-size: 18px;
      line-height: 22px;
      height: 56px;
    }
  }
}
@media (max-width: 640px) {
  .btn-circle {
    width: 32px;
    height: 32px;
    img {
      width: 60px;
      height: 60px;
    }
    svg {
      width: 9px;
      height: 5px;
    }
  }
}
@media (max-width: $mobile-big) {
  .btn {
    &::before {
      display: none;
    }
    &__border {
      &::after {
        display: none;
      }
    }
    &--checkbox {
      &:hover {
        .btn {
          &__border {
            opacity: 0;
          }
        }
        input:checked~.btn__border {
          opacity: 1;
        }
      }
    }
    &--secondary {
      &:hover {
        color: $text-color;
        .btn {
          &__inner {
            svg {
              fill: $text-color;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 640px) {
  .btn {
    &--sm {
      min-width: 168px;
    }
  }
}
@media (max-width: $mobile) {
  .btn {
    font-size: 14px;
    line-height: 17px;
    height: 56px;
    &__border {
      span.tablet {
        display: none;
      }
      span.mob {
        display: inline;
      }
    }
    &--sm {
      height: 40px;
      .btn {
        &__border {
          span.tablet {
            display: none;
          }
          span.mob {
            display: inline;
          }
        }
      }
    }
    &--checkbox {
      min-width: 96px;
      height: 40px;
      font-size: 12px;
      line-height: 15px;
    }
  }
}