.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.apple-fix {
	width: 100%;
	position: fixed;
}

.no-scroll {
	overflow: hidden;
}

.clearfix {
	&::after {
		content: ".";
		display: block;
		clear: both;
		visibility: hidden;
		line-height: 0;
		height: 0;
	}
}

.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	overflow-x: hidden;
	position: relative;
	&.sticky {
		overflow: unset;
	}
	.header {
		flex: 0 0 auto;
	}
	main {
		position: relative;
		flex: 1 0 auto;
	}
	.footer {
		flex: 0 0 auto;
	}
}




// MY____________________________________
.title{
	font-size: 48px;
	font-weight: 900;
	letter-spacing: -1.8px;
	position: relative;
	margin-bottom: 8px;
	z-index: 1;
	&::before{
		position: absolute;
		content: '';
		left: 0;
		top: 50%;
		width: 185px;
		height: 42px;
		transform: translate(-39%,-52%);
		background: url(../img/dots.svg);
		background-size: cover;
		background-position: center;
		z-index: -1;
	}
	span{
		color: $c-red;
	}
}
.subtitle{
	font-size: 14px;
	line-height: 140%;
	font-weight: 400;
	a{
		color: inherit;
		transition: 0.3s;
		&:hover{
			color: $c-red;
		}
	}
}
.section-decor{
	position: absolute;
	right: 0px;
	top: 218px;
	width: 115px;
	z-index: 3;
}
.main-home-page{
	overflow: hidden;
}
// MY____________________________________
@media screen and (max-width: $tablet){
	.title{
		font-size: 28px;
		line-height: 110%;
		text-align: center;
		letter-spacing: 0px;
		margin-bottom: 11px;
		&::before{
			width: 184px;
			height: 42px;
			left: -51px;
			top: 52%;	  
		}
	}
	.subtitle{
		font-size: 14px;
		line-height: 140%;
		text-align: center;
		padding: 0 5px;
	}
}
