.hero{
   position: relative;
   min-height: 800px;
   background: radial-gradient(74.46% 120.71% at 57.53% 21.14%, #410646 0%, #051227 100%);
   z-index: 1;
   position: relative;
   &__container-decor{
      padding-top: 280px;
      position: relative;
      padding-bottom: 42px;
   }
   &__decor{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      img{
         position: absolute;
         bottom: 0;
         left: 220px;
      }
      svg{
         position: absolute;
         bottom: 0;
         left: -58px;
      }
   }

}
.hero-content{
   display: flex;
   width: 100%;
   max-width: 1082px;
   justify-content: space-between;
   &__subtitle{
      display: flex;
      align-items: center;
      margin-bottom: 31px;
      svg{
         margin-right: 15px;
      }
      h2{
         font-weight: 600;
         font-size: 20px;
         line-height: 100%;
         color: white;
      }
   }
   &__title{
      font-weight: bold;
      font-size: 48px;
      line-height: 110%;
      color: white;
      width: 100%;
      max-width: 500px;
      margin-bottom: 30px;
      span{
         color: $c-red;
      }
   }
   &__descr{
      display: flex;
      width: 100%;
      max-width: 282px;
      justify-content: space-between;
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      letter-spacing: 0.14em;
      margin-bottom: 205px;
      p{
         text-transform: uppercase;
         padding-right: 30px;
         position: relative;
         color: rgba(196, 196, 196, 0.5);
         &::after{
            position: absolute;
            content: '';
            top: 50%;
            right: 0;
            transform: translateY(-50%);
            width: 1px;
            height: 9px;
            background: rgba(196, 196, 196, 0.5);
         }
         &:last-child{
            padding-right: 0;
            &::after{
               display: none;
            }
         }

      }
   }
   &__downloads{
      display: flex;
      a{
         display: block;
         padding-left: 32px;
         position: relative;
         margin-right: 40px;
         color: white;
         font-weight: 600;
         font-size: 14px;
         line-height: 17px;
         &:last-child{
            margin-right: 0;
         }
         svg{
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
         }
         &::after{
            position: absolute;
            content: '';
            width: calc(100% - 32px);
            height: 1px; 
            background: white;
            bottom: -2px;
            right: 1px;
            transition: 0.3s;
         }
         &:hover{
            &::after{
               width: 0;
            }
         }
      }
   }
   &__video-icon{
      margin-bottom: 28px;
   }
   &__video-text{
      font-weight: 600;
      font-size: 12px;
      line-height: 15px;
      text-transform: uppercase;
      text-align: center;
      color: white;
      letter-spacing: 2.7px;
      padding-left: 2px;
   }
}



.videoplay-svg{
   &__path1{
      transition: 0.3s;
   }
   &:hover{
      .videoplay-svg__path1{
         fill: #EA77FF;
      }
   }
}
